import React, { useState } from 'react'
import { getImageUrl } from '../../../utils'
import {
  Accordion,
  AccordionButton,
  AccordionItem,
  AccordionPanel,
  Box,
  Button,
  CircularProgress,
  Container,
  Flex,
  Image,
  Progress,
  Skeleton,
  Table,
  Tbody,
  Td,
  Text,
  Th,
  Thead,
  Tr,
  useMediaQuery,
} from '@chakra-ui/react'
import TokenIcon from '../../../components/Svgs/tokenIcons'
import styled from '@emotion/styled'
import Empty from '../../../components/Svgs/empty'
import { updateCollectionViewOpen, updateSelectedCollectionId, updateShowMint } from '../../../state/collection/reducer'
import { useDispatch } from 'react-redux'
import dayjs from 'dayjs'

const StyledTd = styled(Td)`
  padding: 16px 0;
  font-size: 14px;
  font-weight: 500;
`

const StyledTh = styled(Th)`
  padding-left: 0;
  text-transform: unset;
  /* color: ${({ theme }) => theme.colors.text}; */
  font-size: 14px;
`

const StatsTable = ({ data, tableView }) => {
  const [isMinimum968] = useMediaQuery('(min-width: 968px)')

  const dispatch = useDispatch()

  const onClickCollection = (id) => {
    dispatch(updateSelectedCollectionId({ id }))
    dispatch(updateCollectionViewOpen({ collectionViewOpen: true }))
  }

  const onClickMint = ({ collectionId, mintEnabled, isExternalMint, profile }) => {
    if (isExternalMint) {
      window.open(profile.socialmedia.website)
    } else {
      dispatch(updateSelectedCollectionId({ id: collectionId }))
      dispatch(updateShowMint({ showMint: true }))
    }
  }

  if (isMinimum968) {
    return (
      <Table variant="simple" borderCollapse="initial" w="full">
        <Thead>
          <Tr>
            <Th color="tableColumn" textAlign="left" paddingLeft={0}>
              Collection
            </Th>
            <Th color="tableColumn" textAlign="center">
              Launch Date
            </Th>
            <Th color="tableColumn" textAlign="center">
              Total Mints
            </Th>
            <Th color="tableColumn" textAlign="center">
              Mint Price
            </Th>
            {['ongoing', 'free'].includes(tableView) && (
              <Th color="tableColumn" textAlign="center">
                1h Mints
              </Th>
            )}
            {['ongoing'].includes(tableView) && (
              <Th color="tableColumn" textAlign="center">
                Mint %
              </Th>
            )}
            {['ongoing', 'free'].includes(tableView) && (
              <Th color="tableColumn" textAlign="center">
                Unique Minters
              </Th>
            )}
          </Tr>
        </Thead>
        <Tbody>
          {data.length === 0 ? (
            <Tr opacity={0.4}>
              <Td colSpan={7}>
                <Flex flexDir="column" justifyContent={'center'} alignItems="center" mt="8" w="auto">
                  <Empty height="25px" width="25px" />
                  <Text p="2" pb="6" fontSize="xs" textTransform={'uppercase'}>
                    No Data Found
                  </Text>
                </Flex>
              </Td>
            </Tr>
          ) : (
            data.map((data, index) => (
              <Tr key={index}>
                <Td data-title="Collection" paddingLeft={0}>
                  <Flex alignItems="center" onClick={() => onClickCollection(data.collectionId)} cursor="pointer">
                    <Skeleton isLoaded={data.isLoading} fadeDuration={0.1} minW="10" minH="10">
                      <Image
                        src={
                          data?.profile?.profileImage &&
                          getImageUrl(data?.profile?.profileImage, {
                            height: 100,
                            quality: 80,
                          })
                        }
                        w="10"
                        h="10"
                        borderRadius={'4'}
                        alt=""
                      />
                    </Skeleton>
                    <Skeleton
                      ml="2"
                      isLoaded={data.isLoading}
                      fadeDuration={0.1}
                      h={data.isLoading ? 'fit-content' : '16px'}
                      w={data.isLoading ? 'fit-content' : '80px'}
                    >
                      <Text fontSize="14px" fontWeight={'500'} mr="6">
                        {data?.profile?.name}
                      </Text>
                    </Skeleton>
                  </Flex>
                </Td>
                <Td data-title="Launch Date">
                  <Skeleton
                    isLoaded={data.isLoading}
                    fadeDuration={0.1}
                    h={data.isLoading ? 'fit-content' : '16px'}
                    w={data.isLoading ? 'fit-content' : '80px'}
                    mx="auto"
                  >
                    <Text fontSize="14px" fontWeight={'500'}>
                      {tableView === 'upcoming'
                        ? dayjs.unix(data?.stats?.startDate).format('MMM DD YYYY, h:mm a')
                        : data?.stats?.launchedOn}
                    </Text>
                  </Skeleton>
                </Td>
                <Td data-title="Total Mints">
                  <Skeleton
                    isLoaded={data.isLoading}
                    fadeDuration={0.1}
                    h={data.isLoading ? 'fit-content' : '16px'}
                    w={data.isLoading ? 'fit-content' : '80px'}
                    mx="auto"
                  >
                    <Text fontSize="14px" fontWeight={'500'}>
                      {`${data?.stats?.totalSupply}/${data?.stats?.maxSupply}`}
                    </Text>
                  </Skeleton>
                </Td>
                <Td data-title="Mint Price">
                  <Skeleton
                    isLoaded={data.isLoading}
                    fadeDuration={0.1}
                    h={data.isLoading ? 'fit-content' : '16px'}
                    w={data.isLoading ? 'fit-content' : '80px'}
                    mx="auto"
                  >
                    <Flex alignItems="center" justifyContent={'center'}>
                      <TokenIcon width={14} height={14} chainId={data?.contract?.chainId} />
                      <Text fontSize={'14px'} fontWeight={'500'} ml="1" mt={'0 !important'}>
                        {data?.stats?.price?.value ? data?.stats?.price?.value : '--'}
                      </Text>
                    </Flex>
                  </Skeleton>
                </Td>

                {['ongoing', 'free'].includes(tableView) && (
                  <>
                    <Td data-title="1h Mints">
                      <Skeleton
                        isLoaded={data.isLoading}
                        fadeDuration={0.1}
                        h={data.isLoading ? 'fit-content' : '16px'}
                        w={data.isLoading ? 'fit-content' : '80px'}
                        mx="auto"
                      >
                        <Text fontSize="14px" fontWeight={'500'}>
                          {data?.stats?.hourlyMint}
                        </Text>
                      </Skeleton>
                    </Td>

                    {['ongoing'].includes(tableView) && (
                      <Td data-title="Mint %">
                        <Skeleton
                          isLoaded={data.isLoading}
                          fadeDuration={0.1}
                          h={data.isLoading ? 'fit-content' : '16px'}
                          w={data.isLoading ? 'fit-content' : '80px'}
                          mx="auto"
                        >
                          <Text fontSize="14px" fontWeight={'500'}>
                            {data?.stats?.mintPercentage}
                          </Text>
                        </Skeleton>
                      </Td>
                    )}

                    <Td data-title="Unique Minters">
                      <Skeleton
                        isLoaded={data.isLoading}
                        fadeDuration={0.1}
                        h={data.isLoading ? 'fit-content' : '16px'}
                        w={data.isLoading ? 'fit-content' : '80px'}
                        mx="auto"
                      >
                        <Flex justifyContent={'center'} mb="1">
                          <Text fontSize="14px" fontWeight={'500'}>
                            {isNaN(data?.stats?.minters / data?.stats?.totalSupply)
                              ? '0%'
                              : `${((data?.stats?.minters / data?.stats?.totalSupply) * 100).toPrecision(3)}%`}
                          </Text>
                          <Text fontSize="14px" fontWeight={'500'} ml="2">{`(${data?.stats?.minters})`}</Text>
                        </Flex>
                        <Progress
                          h="2"
                          colorScheme="gray"
                          borderRadius={'8'}
                          value={
                            isNaN(data?.stats?.minters / data?.stats?.totalSupply)
                              ? 0
                              : (data?.stats?.minters / data?.stats?.totalSupply) * 100
                          }
                        />
                      </Skeleton>
                    </Td>
                  </>
                )}

                <Td width="70px" paddingRight={0}>
                  {data?.mintEnabled && (
                    <Button background="foreground" color="background" fontSize={14} onClick={() => onClickMint(data)}>
                      Mint
                    </Button>
                  )}
                </Td>
              </Tr>
            ))
          )}
        </Tbody>
      </Table>
    )
  }
  return (
    <Table variant="simple" fontFamily={'Poppins !important'}>
      <Thead>
        <Tr display={'flex'} pb="2">
          <StyledTh w="full" color="tableColumn" border="none" textAlign={'left'}>
            Collection
          </StyledTh>
          <StyledTh w="full" color="tableColumn" border="none" textAlign={'right'}>
            Launch Date
          </StyledTh>
        </Tr>
      </Thead>
      {data.length > 0 ? (
        <Box>
          <Accordion allowToggle>
            {data.map((data, index) => (
              <Tr
                w="full"
                key={index}
                style={{
                  display: 'flex',
                }}
              >
                <AccordionItem w="full">
                  <AccordionButton w="full" px="0">
                    <Td w="full" border="none" p="0">
                      <h2>
                        <Flex alignItems={'center'} justifyContent="space-between">
                          <Box border="none" overflow={'hidden'} onClick={() => onClickCollection(data.collectionId)}>
                            <Flex alignItems="center">
                              <Skeleton isLoaded={data.isLoading} minW="10" mintH="10" fadeDuration={0.1}>
                                <Image
                                  src={
                                    data?.profile?.profileImage &&
                                    getImageUrl(data?.profile?.profileImage, {
                                      height: 100,
                                      quality: 80,
                                    })
                                  }
                                  w="10"
                                  h="10"
                                  borderRadius={'4'}
                                  alt=""
                                />
                              </Skeleton>
                              <Skeleton
                                ml="2"
                                isLoaded={data.isLoading}
                                fadeDuration={0.1}
                                h={data.isLoading ? 'fit-content' : '16px'}
                                w={data.isLoading ? 'fit-content' : '80px'}
                              >
                                <Text
                                  fontSize="14px"
                                  fontWeight={'500'}
                                  mr="6"
                                  noOfLines={2}
                                  textOverflow={'ellipsis'}
                                  overflow="hidden"
                                >
                                  {data?.profile?.name}
                                </Text>
                              </Skeleton>
                            </Flex>
                          </Box>
                          <Text textAlign={'center'} border="none">
                            <Skeleton
                              isLoaded={data.isLoading}
                              fadeDuration={0.1}
                              h={data.isLoading ? 'fit-content' : '16px'}
                              w={data.isLoading ? 'fit-content' : '80px'}
                            >
                              <Text fontSize="14px" fontWeight={'500'}>
                                {tableView === 'upcoming'
                                  ? dayjs.unix(data?.stats?.startDate).format('MMM DD YYYY, h:mm a')
                                  : data?.stats?.launchedOn}
                              </Text>
                            </Skeleton>
                          </Text>
                        </Flex>
                      </h2>
                      {data?.mintEnabled && (
                        <Button
                          width="100%"
                          marginTop={3}
                          background="foreground"
                          color="background"
                          fontSize={14}
                          onClick={() => onClickMint(data)}
                        >
                          Mint
                        </Button>
                      )}
                    </Td>
                  </AccordionButton>
                  <AccordionPanel pb={4} w={'full'} overflowX={'scroll'}>
                    <Flex justifyContent={'space-between'} w={['160%', '100%']}>
                      <Box>
                        <Text color="tableColumn" textAlign={'center'} fontSize="14px" fontWeight={'500'}>
                          Total Mints
                        </Text>
                        <Text
                          textAlign={'center'}
                          fontSize="sm"
                          fontWeight={'500'}
                        >{`${data?.stats?.totalSupply}/${data?.stats?.maxSupply}`}</Text>
                      </Box>
                      <Box>
                        <Text color="tableColumn" textAlign={'center'} fontSize="14px" fontWeight={'500'}>
                          Mint Price
                        </Text>
                        <Flex justifyContent="center" alignItems="center">
                          <TokenIcon width={14} height={14} chainId={data?.contract?.chainId} />
                          <Text textAlign={'center'} fontSize="sm" fontWeight={'500'}>
                            {data?.stats?.price?.value}
                          </Text>
                        </Flex>
                      </Box>
                      {['ongoing', 'free'].includes(tableView) && (
                        <>
                          <Box>
                            <Text color="tableColumn" textAlign={'center'} fontSize="14px" fontWeight={'500'}>
                              1h Mints
                            </Text>
                            <Text textAlign={'center'} fontSize="sm" fontWeight={'500'}>
                              {data?.stats?.hourlyMint}
                            </Text>
                          </Box>
                          <Box>
                            <Text color="tableColumn" textAlign={'center'} fontSize="14px" fontWeight={'500'}>
                              Mint %
                            </Text>
                            <Text textAlign={'center'} fontSize="sm" fontWeight={'500'}>
                              {data?.stats?.mintPercentage}
                            </Text>
                          </Box>
                          <Box>
                            <Text color="tableColumn" textAlign={'center'} fontSize="14px" fontWeight={'500'}>
                              Unique Minters
                            </Text>
                            <Flex justifyContent={'center'} mb="1">
                              <Text fontSize="14px" fontWeight={'500'}>
                                {isNaN(data?.stats?.minters / data?.stats?.totalSupply)
                                  ? '0%'
                                  : `${((data?.stats?.minters / data?.stats?.totalSupply) * 100).toPrecision(3)}%`}
                              </Text>
                              <Text fontSize="14px" fontWeight={'500'} ml="2">{`(${data?.stats?.minters})`}</Text>
                            </Flex>
                            <Progress
                              h="2"
                              colorScheme="gray"
                              borderRadius={'8'}
                              value={
                                isNaN(data?.stats?.minters / data?.stats?.totalSupply)
                                  ? 0
                                  : (data?.stats?.minters / data?.stats?.totalSupply) * 100
                              }
                            />
                          </Box>
                        </>
                      )}
                    </Flex>
                  </AccordionPanel>
                </AccordionItem>
              </Tr>
            ))}
          </Accordion>
        </Box>
      ) : (
        <Tr opacity={0.4}>
          <Td>
            <Flex flexDir="column" justifyContent={'center'} alignItems="center" mt="4">
              <Empty height="25px" width="25px" />
              <Text p="2" pb="6" fontSize="xs" textTransform={'uppercase'}>
                No Data Found
              </Text>
            </Flex>
          </Td>
        </Tr>
      )}
    </Table>
  )
}

export default StatsTable
