// truncate address
export const truncateAddress = (address, digits) => {
  if (address) {
    return address.length > digits
      ? address.substr(0, digits - 1) + '...' + address.substr(address.length - (digits - 1), address.length - 1)
      : address
  } else {
    return ''
  }
}

// make friendly number
export const makeFriendlyNumber = (number) => {
  return new Intl.NumberFormat('en-GB', {
    notation: 'compact',
    compactDisplay: 'short',
  }).format(number)
}

// copy text
export const copyText = (text, cb) => {
  if (navigator.clipboard && navigator.permissions) {
    navigator.clipboard.writeText(text).then(cb)
  } else if (document.queryCommandSupported('copy')) {
    const ele = document.createElement('textarea')
    ele.value = text
    document.body.appendChild(ele)
    ele.select()
    document.execCommand('copy')
    document.body.removeChild(ele)
    cb?.()
  }
}

// extract url from iframe tag if iframe exist
export const extractSrc = (str) => {
  const regex = /<iframe.*src="([^"]+)".*>/
  const match = str.match(regex)
  if (match) {
    return match[1]
  }
  return null
}

export function toCamelize(str) {
  return str
    .replace(/(?:^\w|[A-Z]|\b\w)/g, function (word, index) {
      return index === 0 ? word.toLowerCase() : word.toUpperCase()
    })
    .replace(/\s+/g, '')
}

export function numberToWords(num) {
  const ones = ['', 'one', 'two', 'three', 'four', 'five', 'six', 'seven', 'eight', 'nine']
  const tens = ['', 'ten', 'twenty', 'thirty', 'forty', 'fifty', 'sixty', 'seventy', 'eighty', 'ninety']
  const teens = [
    'ten',
    'eleven',
    'twelve',
    'thirteen',
    'fourteen',
    'fifteen',
    'sixteen',
    'seventeen',
    'eighteen',
    'nineteen',
  ]

  if (num === 0) {
    return 'zero'
  }

  let words = ''

  if (num < 0) {
    words += 'negative '
    num = Math.abs(num)
  }

  if (num >= 1000000) {
    words += numberToWords(Math.floor(num / 1000000)) + ' million '
    num %= 1000000
  }

  if (num >= 1000) {
    words += numberToWords(Math.floor(num / 1000)) + ' thousand '
    num %= 1000
  }

  if (num >= 100) {
    words += ones[Math.floor(num / 100)] + ' hundred '
    num %= 100
  }

  if (num >= 20) {
    words += tens[Math.floor(num / 10)] + ' '
    num %= 10
  } else if (num >= 10) {
    words += teens[num - 10] + ' '
    num = 0
  }

  if (num > 0) {
    words += ones[num]
  }

  return words.trim()
}
